export default {
    name: "CuiVideo",
    data()
    {
        return {
            playImg:require("../../../assets/image/play.png"),
            isPaused:true,
            show:true,
            styleCss:{},
            url:this.$route.query.url,
            isH:false,//是否横屏
        }
    },
    created()
    {
        console.log(999999)
    },
    beforeRouteLeave(to,from,next)
    {
        //from.meta.keepAlive=false;
        to.meta.keepAlive=true;
        console.log(11111111)
        console.log(to)
        console.log(from)
        next();
    },
    methods:{
        play()
        {
            let myVideo=this.$refs["video"];
            if (myVideo.paused)
            {
                myVideo.play();
                this.isPaused=false;
            }
            else
            {
                myVideo.pause();
                this.isPaused=true;
            }
        },
        done()
        {
            this.isPaused=true;
        },
        closeVideo()
        {

            this.isPaused=true;
            this.styleCss={};
            this.$emit("close");
        },
        playing(e)
        {
        },
        changeH()
        {
            if(!this.isH)
            {
                this.isH=true;
                let myVideo=this.$refs["video"],
                    width=document.documentElement.clientWidth,
                    height=document.documentElement.clientHeight;
                this.styleCss={
                    width:height+"px",
                    height:width+"px",
                    transform:"rotate(90deg)",
                    marginLeft:"-"+(height-width)/2+"px"
                }
            }
            else
            {
                this.isH=false;
                this.styleCss={};
            }
        }
    }
}



